body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0; }

.main-wrapper {
  display: grid;
  grid-template-areas: "h h h" "m m m" "f f f";
  grid-template-rows: 1fr 8fr 1fr; }

.header-first-wrapper-child {
  grid-area: h; }

.main-first-wrapper-child {
  grid-area: m; }

.footer-first-wrapper-child {
  grid-area: f; }

.welcome {
  color: darkblue; }

header ul {
  width: 100%;
  background: #013369;
  display: inline-block; }

header ul li {
  color: white;
  display: inline-block;
  padding: 10px; }

.stat-box-container {
  background: whitesmoke;
  align-items: center;
  background-image: "https://cdn.arstechnica.net/wp-content/uploads/2017/07/GettyImages-626201510.jpg"; }

.stat-box {
  background: white;
  width: 80%;
  margin-top: 40px; }

